mlist: [],\n      authList: [],\n      curId: 0,\n      threeAuthList: [],\n      seCurId: 0,\n      thCurId: 0,\n      materielData: [],\n      region: 0,\n      regionInfo: [],\n      detailsVisible: false,\n      arealist: []\n    }\n\n  },\n  methods: {\n\n    paging (e) {\n      this.page = e;\n      this.init();\n    },\n    init: function () {\n      this.$axios.post('admin/AgentAuditList/index', { token: this.token, page: this.page, status: 3 })\n        .then(res => {\n          if (res.data.data.list) {\n            this.list = res.data.data.list;\n            this.pageCount = res.data.data.rows;\n          }\n\n        })\n        .catch(err => { })\n    },\n    async details (row) {\n      try {\n        const res = await this.$axios.post('admin/AgentAuditList/showArea', { token: this.token, aid: row.aid, id: row.id })\n        this.arealist = res.data.data\n\n      } catch (error) {\n        throw (error)\n      }\n    },\n  },\n  mounted () {\n    var id = this.$route.query.id;\n    this.curId = id;\n    this.$axios.post('admin/Auth/erAuth', {\n      token: window.sessionStorage.getItem('bbytoken'),\n      id: id\n    })\n      .then(res => {\n\n        if (res.data.code == 1) {\n          var arr = res.data.data;\n          for (var i = 0; i < arr.length; i++) {\n            if (arr[i].son) {\n              if (arr[i].name == '注册审核') {\n                this.seCurId = arr[i].id;\n                this.threeAuthList = arr[i].son;\n              }\n              for (var j = 0; j < arr[i].son.length; j++) {\n                if (arr[i].action != arr[i].son[j].action) {\n                  arr[i].action = arr[i].son[0].action;\n                }\n                if (arr[i].son[j].name == '已驳回' && arr[i].name == '注册审核') {\n                  this.thCurId = arr[i].son[j].id;\n                }\n              }\n            }\n          }\n          this.authList = arr;\n        } else {\n          this.$alert(res.data.msg, '提示', { type: 'error' });\n        }\n      })\n      .catch(err => {\n        alert(err);\n      })\n  },\n  created () {\n    this.init();\n  },\n  watch: {\n    index: function () {\n      this.init();\n    }\n  }\n}\n</script>\n<style scoped>\n.ellipsis {\n  width: 70px;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  display: -webkit-box;\n  -webkit-box-orient: vertical;\n  -webkit-line-clamp: 2;\n  margin: 0 auto;\n}\n.regionDetail >>> .el-dialog__title {\n  font-size: 22px;\n}\n.regionDetail >>> .el-dialog__body {\n  padding: 0px 25px 30px;\n  border-top: solid 2px #eee;\n}\n</style>\n"],"sourceRoot":""}]);

